<template>
    <transition name="transform">
        <div v-show="showPdf" class="web-pdf background-color">
            <div class="top-title">
                导出预览
                <div class="handler-button">
                    <el-button
                        @click="confirm"
                        size="small"
                        type="primary"
                        icon="el-icon-check"
                        >确认导出</el-button
                    >
                    <el-button
                        @click="$store.commit('updateShowPdf', false)"
                        size="small"
                        type="danger"
                        icon="el-icon-close"
                        >取消</el-button
                    >
                </div>
            </div>
            <div class="bottom-list">
                <div class="bottom-component" v-show="chatList.length > 0">
                    <div class="name">
                        <div class="name-box">
                            {{
                                this.checkedConversationInfo
                                    ? this.checkedConversationInfo.name
                                    : ''
                            }}
                        </div>
                    </div>
                    <div
                        class="bottom-item"
                        v-for="(item, index) in chatList"
                        :key="index"
                    >
                        <div class="item-top">
                            <div class="top-name title-color">
                                {{ item.name }}
                            </div>
                            <div class="top-time text-color">
                                {{ item.time }}
                            </div>
                        </div>
                        <div class="item-bottom text-color">
                            {{ item.content }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
import { mapState } from 'vuex'
import { handlerDate } from '@/utils/time.js'
import html2Canvas from 'html2canvas'
import JsPDF from 'jspdf'
export default {
    computed: {
        ...mapState({
            showPdf: (state) => state.showPdf,
            checkedConversationInfo: (state) => state.checkedConversationInfo,
            outImportMessage: (state) => state.outImportMessage,
        }),
    },
    watch: {
        showPdf(val) {
            if (this.outImportMessage) return
            console.log('showPdf------------------------》')
            if (val) {
                this.importChat()
            } else {
                this.chatList = []
            }
        },
        checkedConversationInfo: {
            async handler(val) {
                let self = this
                if (val && self.outImportMessage) {
                    console.log('消息盒子外部触发了该方法，导出消息')
                    self.chatList = []
                    await self.handlerMessage().then(() => {
                        self.$store.commit('updateImportMessage', false)
                        window.parent.postMessage(
                            { importList: self.chatList },
                            '*'
                        )
                    })
                    console.log(
                        '获取到所有的消息数据------------------------->',
                        self.chatList
                    )
                }
            },
            deep: true,
        },
    },
    data() {
        return {
            chatList: [],
        }
    },
    methods: {
        // 聊天记录导出
        async importChat() {
            console.log('导出记录')
            this.$asyncLoading(async () => {
                await this.handlerMessage()
            })
        },
        async handlerMessage() {
            let isComplate = false
            let lastSep = null
            let arr = []
            while (!isComplate) {
                let data = {
                    GroupId: this.checkedConversationInfo.userOrGroupId,
                    ReqMsgNumber: 20,
                    ReqMsgSeq: lastSep,
                }
                if (!lastSep) delete data.lastSep
                let res = await this.$apis.selectChat(data)
                console.log('获取未处理的消息--------------------------->', res)
                arr = arr.concat(res.RspMsgList)
                lastSep = res.RspMsgList[res.RspMsgList.length - 1].MsgSeq
                if (lastSep === 1) {
                    isComplate = true
                }
            }
            let userIdList = []
            let valueList = []
            let userInfoList = []
            valueList = arr.map((ele) => {
                if (ele.From_Account === '@TIM#SYSTEM') {
                    return {
                        name: '群系统消息',
                        time: handlerDate(ele.MsgTimeStamp, true),
                        content: this.handlerGroup(ele),
                        type: ele.From_Account,
                        id: ele.From_Account,
                    }
                } else {
                    console.log('ele------------------------>', ele)
                    return {
                        name: '',
                        time: handlerDate(ele.MsgTimeStamp, true),
                        content: ele.MsgBody[0]
                            ? ele.MsgBody[0].MsgType !== 'TIMCustomElem'
                                ? ele.MsgBody[0].MsgContent.Text
                                : '群组创建'
                            : '--',
                        type: ele.MsgBody[0] ? ele.MsgBody[0].MsgType : '',
                        id: ele.From_Account,
                    }
                }
            })
            userIdList = valueList
                .map((ele) => {
                    if (ele.type === 'TIMTextElem') {
                        return ele.id
                    }
                })
                .filter((ele) => ele)
            userIdList = Array.from(new Set(userIdList))
            let userResrArr = await this.$tim.getUserProfile({
                userIDList: userIdList,
            })
            userInfoList = userResrArr.data.map((ele) => {
                return {
                    id: ele.userID,
                    name: ele.nick,
                }
            })
            for (const i of valueList) {
                if (userInfoList.findIndex((ele) => ele.id === i.id) !== -1) {
                    let index = userInfoList.findIndex((ele) => ele.id === i.id)
                    i.name = userInfoList[index].name
                }
            }
            this.chatList = valueList
            this.chatList = this.chatList.filter(
                (ele) => ele.content.indexOf('游客_') === -1
            )
        },
        // 群系统消息处理
        handlerGroup(item) {
            let str = ''
            let type = item.MsgBody.OpType
            switch (type) {
                case 6:
                    str = `群公告：${item.MsgBody.MsgGroupNewInfo.GroupIntroduction}`
                    break
                case 1:
                    str = `${item.MsgBody.MsgMemberExtraInfo.map((ele) => {
                        return ele.NickName
                    }).join(',')}加入房间`
                    break
                case 7:
                    console.log(
                        item.MsgBody.MsgMemberExtraInfo.map((ele) => {
                            return ele.NickName
                        }).join(','),
                        '--------------------------------------------------------------->'
                    )
                    if (item.MsgBody.MsgMemberInfo[0].ShutupTime > 0) {
                        str = `${item.MsgBody.MsgMemberExtraInfo.map((ele) => {
                            return ele.NickName
                        }).join(',')}已被禁言`
                    } else {
                        str = `${item.MsgBody.MsgMemberExtraInfo.map((ele) => {
                            return ele.NickName
                        }).join(',')}已被解除禁言`
                    }

                    break
                case 3:
                    str = `${item.MsgBody.MsgMemberExtraInfo.map((ele) => {
                        return ele.NickName
                    }).join(',')}被移除房间`
                    break
            }
            return str
        },
        // 确认导出
        confirm() {
            if (this.chatList.length === 0) {
                this.$notify({
                    title: '警告',
                    message: '当前无聊天消息可导出',
                    type: 'warning',
                })
                window.parent.postMessage(
                    { noMessage: '当前无聊天消息可导出' },
                    '*'
                )
                return
            }
            this.$asyncLoading(async () => {
                await this.clickConfirm()
            })
        },
        async clickConfirm() {
            let self = this
            let element = document.querySelector('.bottom-component')
            let canvas = await html2Canvas(element, {
                logging: false,
                allowTaint: true,
            })
            var pdf = new JsPDF('p', 'mm', 'a4') //A4纸，纵向
            var ctx = canvas.getContext('2d'),
                a4w = 190,
                a4h = 277, //A4大小，210mm x 297mm，四边各保留10mm的边距，显示区域190x277
                imgHeight = Math.floor((a4h * canvas.width) / a4w), //按A4显示比例换算一页图像的像素高度
                renderedHeight = 0

            while (renderedHeight < canvas.height) {
                var page = document.createElement('canvas')
                page.width = canvas.width
                page.height = Math.min(
                    imgHeight,
                    canvas.height - renderedHeight
                ) //可能内容不足一页
                //用getImageData剪裁指定区域，并画到前面创建的canvas对象中
                page.getContext('2d').putImageData(
                    ctx.getImageData(
                        0,
                        renderedHeight,
                        canvas.width,
                        Math.min(imgHeight, canvas.height - renderedHeight)
                    ),
                    0,
                    0
                )
                pdf.addImage(
                    page.toDataURL('image/jpeg', 1.0),
                    'JPEG',
                    10,
                    10,
                    a4w,
                    Math.min(a4h, (a4w * page.height) / page.width)
                ) //添加图像到页面，保留10mm边距

                renderedHeight += imgHeight
                if (renderedHeight < canvas.height) pdf.addPage() //如果后面还有内容，添加一个空页
                // delete page;
            }
            pdf.save(self.checkedConversationInfo.name + '.pdf')
            console.log(
                self.checkedConversationInfo.name +
                    '聊天记录' +
                    '.pdf文件导出成功------------------------------------------------------------->'
            )
        },
    },
}
</script>
<style lang="less" scoped>
@import './web-pdf.less';
</style>
