<template>
    <div
        class="web-conversation border-color"
        :style="{ width: !isLeftHide ? '30%' : '88px' }"
    >
        <div class="conversation-list">
            <div
                :class="[
                    'conversation-item',
                    checkedConversationInfo &&
                        checkedConversationInfo.conversationID ===
                            item.conversationID &&
                        'check-item',
                ]"
                v-for="(item, index) in conversations"
                :key="index"
                @click="toggleConversation(item)"
            >
                <div class="conversation-item_avatar">
                    <el-tooltip
                        popper-class="max-width"
                        effect="dark"
                        :content="item.name"
                        placement="left"
                        :disabled="!isLeftHide"
                    >
                        <img :src="item.avatar" />
                    </el-tooltip>
                    <div class="unread-count" v-if="item.unreadCount > 0">
                        {{ item.unreadCount }}
                    </div>
                </div>
                <div class="conversation-item_info" v-show="!isLeftHide">
                    <div class="info-name-time">
                        <div class="info-name title-color text-one">
                            {{ item.name }}
                        </div>
                        <div class="info-time text-color text-one">
                            {{ item.time }}
                        </div>
                    </div>
                    <div class="info-content text-color text-one">
                        {{ item.content }}
                    </div>
                </div>
            </div>
        </div>
        <div
            class="show-or-hide background-color"
            @click="$store.commit('updateIsLeftHide', !isLeftHide)"
        >
            <img
                :style="{
                    transform: !isLeftHide ? 'rotate(180deg)' : '',
                }"
                src="@/assets/arraw-right.png"
            />
        </div>
    </div>
</template>
<script>
import { mapState } from "vuex";
import { handlerDate } from "@/utils/time";
export default {
    data() {
        return {
            name: "conversation",
            conversations: [],
        };
    },
    computed: {
        ...mapState({
            userInfo: (state) => state.userInfo,
            isRightHide: (state) => state.isRightHide,
            isLeftHide: (state) => state.isLeftHide,
            checkedConversationInfo: (state) => state.checkedConversationInfo,
            theme: (state) => state.theme,
        }),
    },
    watch: {
        isRightHide(value) {
            if (!value) {
                this.$store.commit("updateIsLeftHide", true);
            }
        },
    },
    methods: {
        // 获取会话列表
        async getConversationList() {
            this.conversations = [];
            let res = await this.$tim.getConversationList();
            console.log(
                "进入方法调用获取绘画类表-------------------------------------------------------》",
                res.data.conversationList
            );
            let arr = [];
            for (const ele of res.data.conversationList) {
                if (ele.type !== "@TIM#SYSTEM") {
                    let data = {
                        conversationID: ele.conversationID,
                        type: ele.type,
                        userOrGroupId:
                            ele.type == this.TIM.TYPES.CONV_C2C
                                ? ele.userProfile.userID
                                : ele.groupProfile.groupID,
                        unreadCount: ele.unreadCount,
                        content: this.handlerMessage(ele),
                        time: handlerDate(ele.lastMessage.lastTime),
                        avatar: await this.getAvatar(ele),
                        name:
                            ele.type === this.TIM.TYPES.CONV_C2C
                                ? ele.userProfile.nick
                                : this.userInfo.roomName,
                        selfInfo:
                            ele.type === this.TIM.TYPES.CONV_C2C
                                ? null
                                : ele.groupProfile.selfInfo,
                    };
                    arr.push(data);
                }
            }
            this.conversations = arr.map((ele) => {
                if (
                    ele.type === "GROUP" &&
                    ele.userOrGroupId !== this.userInfo.roomId
                )
                    return;
                return ele;
            });
            console.log('this.conversations================>',this.conversations)
            this.conversations = this.conversations.filter((ele) => ele);
            console.log(
                this.conversations,
                "处理后的数据==============================>"
            );
            let index = this.conversations.findIndex(
                (ele) => ele.userOrGroupId === this.userInfo.roomId
            );
            if (index !== -1 && !this.checkedConversationInfo) {
                this.$store.commit(
                    "updateCheckedConversationInfo",
                    this.conversations[index]
                );
                // this.$parent.udpateUnReadMessage();
            }
        },
        async getAvatar(ele) {
            let url = "";
            if (ele.type !== this.TIM.TYPES.CONV_C2C) {
                url =
                    this.theme === 0
                        ? require("@/assets/group-icon.png")
                        : require("@/assets/dark-group-icon.png");
            } else {
                url = await this.getWords(ele.userProfile.userID);
            }
            return url;
        },
        // 获取用户自定义字段
        async getWords(userID) {
            console.log(userID, "asdggggggggggggggggk=>");
            let res = await this.$tim.getUserProfile({
                userIDList: [userID],
            });
            console.log("数据获取成=============================>", res);
            let arr = res.data[0].profileCustomField;
            let avatar = this.handlerAvatar(
                this.getUserCustomWord(arr, "Tag_Profile_Custom_sex")
            );
            console.log(
                avatar,
                "头像为=====================================》"
            );
            return avatar;
        },
        getUserCustomWord(arr, key) {
            console.log("自定义字段===============>", arr);
            let str = "1";
            for (const i of arr) {
                if (i.key === key) {
                    str = i.value;
                }
            }
            console.log(`${key}------------------->${str}`);
            return str;
        },
        // 处理头像
        handlerAvatar(num) {
            let str = require("@/assets/man-icon.png");
            if (num === "1") {
                str =
                    this.theme === 0
                        ? require("@/assets/man-icon.png")
                        : require("@/assets/dark-man-icon.png");
            } else if (num === "0") {
                str =
                    this.theme === 0
                        ? require("@/assets/wuman-icon.png")
                        : require("@/assets/dark-wuman-icon.png");
            }
            return str;
        },
        // 处理最后一条消息的展示内容
        handlerMessage(item) {
            let msgStr = "";
            if (item.type === this.TIM.TYPES.CONV_C2C) {
                msgStr = item.lastMessage.payload.data
                    ? item.lastMessage.payload.extension
                    : item.lastMessage.messageForShow;
            } else {
                console.log(item);
                if (
                    item.lastMessage.payload &&
                    item.lastMessage.payload.data === "group_create"
                ) {
                    msgStr =
                        "创" +
                        item.lastMessage.payload.extension.split("创")[1];
                } else {
                    msgStr = item.lastMessage.messageForShow;
                }
            }
            return msgStr;
        },
        toggleConversation(item) {
            this.$store.commit("updateCheckedConversationInfo", item);
            this.$parent.sendMsgToParent(this.$parent.listenType, {});
            this.$store.commit("updateIsRightHide", true);
        },
    },
};
</script>
<style lang="less" scoped>
@import "./web-conversation.less";
</style>
