<template>
    <div class="web-message-input">
        <textarea
            class="text-area"
            :placeholder="isJinyan ? '禁言中' : '请输入···'"
            v-model="inputValue"
            :disabled="isJinyan"
            @keydown.enter="sendMessage"
        />
        <div class="send-btn">
            <el-button
                :disabled="isJinyan"
                :loading="loading"
                @click="sendMessage"
            >
                <img v-show="!loading" src="@/assets/send-icon.png" />发送
            </el-button>
        </div>
    </div>
</template>
<script>
import { mapState } from "vuex";
export default {
    computed: {
        ...mapState({
            checkedConversationInfo: (state) => state.checkedConversationInfo,
            theme: (state) => state.theme,
        }),
    },
    watch: {
        checkedConversationInfo: {
            handler(val) {
                this.inputValue = "";
                this.isJinyan = false;
                if (val.type === "C2C") return;
                this.handlerJinyan();
            },
            deep: true,
            immediate: true,
        },
    },
    data() {
        return {
            inputValue: "",
            isJinyan: false,
            loading: false,
        };
    },
    methods: {
        // 是否禁言
        async handlerJinyan() {
            let res = await this.$apis.selectJinyan({
                GroupId: this.checkedConversationInfo.userOrGroupId,
            });
            let arr = res.ShuttedUinList;
            let is = arr.find(
                (ele) => ele.Member_Account === this.$parent.userInfo.userID
            );
            if (is) {
                this.isJinyan = true;
            } else {
                this.isJinyan = false;
            }
        },
        enter(event) {
            console.log("asdasdsadsa", event);
        },
        async sendMessage(event) {
            event.preventDefault();
            this.inputValue = this.inputValue.replace(/(^\s*)|(\s*$)/g, "");
            if (!this.inputValue) {
                return this.$notify({
                    type: "error",
                    title: "系统消息",
                    message: "不允许发送空的消息",
                });
            }
            this.loading = true;
            let message = this.$tim.createTextMessage({
                to: this.checkedConversationInfo.userOrGroupId,
                conversationType: this.checkedConversationInfo.type,
                payload: {
                    text: this.inputValue,
                },
            });
            this.inputValue = "";
            let res = await this.$tim.sendMessage(message);
            let userRes = await this.$parent.getMemberInfo([
                res.data.message.from,
            ]);
            res.data.message.nick = userRes[0].nick;
            res.data.message.avatar =
                userRes[0].profileCustomField[0].value === "1"
                    ? this.theme === 0
                        ? require("@/assets/man-icon.png")
                        : require("@/assets/dark-man-icon.png")
                    : this.theme === 0
                    ? require("@/assets/wuman-icon.png")
                    : require("@/assets/dark-wuman-icon.png");
            this.$parent.messageList = this.$parent.messageList.concat([
                res.data.message,
            ]);
            this.loading = false;
        },
    },
};
</script>
<style lang="less" scoped>
@import "./web-message-input.less";
</style>
