<template>
    <div class="web-message">
        <web-message-name />
        <div class="web-bottom">
            <div
                class="bottom-left"
                :style="{
                    width: !isRightHide
                        ? 'calc(100% - 280px)'
                        : 'calc(100% - 2px)',
                }"
            >
                <div
                    class="message-list"
                    :style="{
                        height:
                            userInfo.userRoleNum === 3
                                ? '100%'
                                : 'calc(100% - 160px)',
                    }"
                    ref="scrollBox"
                >
                    <div
                        class="message-item"
                        v-for="(item, index) in messageList"
                        :key="index"
                        ref="scrollElement"
                    >
                        <div
                            class="message-item_time"
                            v-if="
                                isShowTime(
                                    messageList[index].time,
                                    messageList[index + 1]
                                        ? messageList[index + 1].time
                                        : parseInt(new Date().getTime() / 1000)
                                )
                            "
                        >
                            {{ handlerDate(item.time) }}
                        </div>
                        <div
                            v-if="item.isSysytem"
                            class="message-item_system"
                            v-html="item.sysytemMsg"
                        ></div>
                        <div
                            v-if="!item.isSysytem"
                            :class="[
                                'message-item_info',
                                item.flow === 'in' ? 'left' : 'right',
                            ]"
                        >
                            <div class="info-avatar">
                                <img v-show="item.avatar" :src="item.avatar" />
                            </div>
                            <div class="info-name-content">
                                <div
                                    class="info-name"
                                    v-if="
                                        checkedConversationInfo.type == 'GROUP'
                                    "
                                >
                                    {{ item.nick }}
                                </div>
                                <div
                                    v-if="
                                        item.type === TIM.TYPES.MSG_TEXT ||
                                            item.type === TIM.TYPES.MSG_CUSTOM
                                    "
                                    class="info-content"
                                >
                                    {{ item.payload.text }}
                                </div>
                                <div
                                    v-else-if="item.type === TIM.TYPES.MSG_FILE"
                                    class="info-file"
                                >
                                    <div class="info-file_img">
                                        <img
                                            :src="
                                                typeHandler(
                                                    item.payload.fileName
                                                )
                                            "
                                        />
                                    </div>
                                    <div class="info-file_info">
                                        <div class="name">
                                            {{ item.payload.fileName }}
                                        </div>
                                        <div class="size">
                                            {{
                                                fileSize(item.payload.fileSize)
                                            }}
                                        </div>
                                        <div>
                                            <el-link
                                                v-if="item.flow === 'in'"
                                                :underline="false"
                                                :style="{
                                                    color:
                                                        item.flow === 'in'
                                                            ? '#418fe8'
                                                            : '#ffffff',
                                                }"
                                                @click="downLoad(item.payload)"
                                                icon="el-icon-download"
                                                >下载到本地</el-link
                                            >
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <web-message-input
                    v-show="userInfo.userRoleNum !== 3"
                    :loading="loading"
                />
            </div>
            <web-member
                v-show="
                    userInfo &&
                        userInfo.userRoleNum !== 3 &&
                        checkedConversationInfo.type === 'GROUP'
                "
            />
        </div>
    </div>
</template>
<script>
import { mapState } from "vuex";
import { handlerDate } from "@/utils/time";
// 引入组件
import webMessageName from "@/components/web-message-name/web-message-name";
import webMessageInput from "@/components/web-message-input/web-message-input";
import webMember from "@/components/web-member/web-member";
import axios from "axios";
export default {
    components: {
        webMessageName,
        webMessageInput,
        webMember,
    },
    computed: {
        ...mapState({
            isRightHide: (state) => state.isRightHide,
            isLeftHide: (state) => state.isLeftHide,
            checkedConversationInfo: (state) => state.checkedConversationInfo,
            userInfo: (state) => state.userInfo,
        }),
    },
    watch: {
        checkedConversationInfo: {
            async handler() {
                this.memberList = [];
                this.$asyncLoading(async () => {
                    await this.getMessageList();
                });
            },
            deep: true,
            immediate: true,
        },
    },
    data() {
        return {
            handlerDate,
            name: "message",
            loading: false,
            messageList: [],
            elememt: this.$parent.getChild("conversation"),
            selfAvatar: null,
            noselfAvatar: null,
        };
    },
    updated() {
        this.scrollBottom();
        console.log(this.$children,'asdddddddddddddddddddddd')
        this.$children.forEach(ele=>{
            if(ele.name === 'member') {
                ele.getScrollBar()
            }
        })
    },
    methods: {
        // 获取消息列表
        async getMessageList() {
            let messageRes = await this.$tim.getMessageList({
                conversationID: this.checkedConversationInfo.conversationID,
            });
            let arr = messageRes.data.messageList;
            console.log(
                "收到了消息====================================>",
                arr,
                this.elememt
            );
            if (arr.length !== 0) {
                let type = this.checkedConversationInfo.type;
                if (type === this.TIM.TYPES.CONV_C2C) {
                    this.selfAvatar = await this.elememt.getWords(
                        this.userInfo.userID
                    );
                    console.log(
                        "this.elememt.getWords(arr[0].from fromAvatar===============================>",
                        this.selfAvatar
                    );
                    this.noselfAvatar = await this.elememt.getWords(
                        this.checkedConversationInfo.userOrGroupId
                    );
                    console.log(
                        "this.elememt.getWords(arr[0].to toAvatar===============================>",
                        this.noselfAvatar
                    );
                }
                for (const item of arr) {
                    if (type !== this.TIM.TYPES.CONV_C2C) {
                        if (item.payload.data === "group_create") {
                            this.$set(item, "isSysytem", true);
                            this.$set(
                                item,
                                "sysytemMsg",
                                "创" + item.payload.extension.split("创")[1]
                            );
                        } else {
                            this.handlerGROUP(item);
                        }
                    } else {
                        this.handlerC2C(item);
                        console.log(
                            "item.from === this.userInfo.userID============>from,to",
                            item.from === this.userInfo.userID
                                ? this.fromAvatar
                                : this.toAvatar,
                            item,
                            item.from,
                            this.userInfo.userID,
                            item.from === this.userInfo.userID
                        );
                        this.$set(
                            item,
                            "avatar",
                            item.flow === "out"
                                ? this.selfAvatar
                                : this.noselfAvatar
                        );
                    }
                }
            }
            this.messageList = arr;
            console.log(
                "获取到了消息列表===============================>",
                this.messageList
            );
        },
        // 处理了消息
        async handlerGROUP(item) {
            if (item.type === "TIMGroupTipElem") {
                let data = await this.handlerGroupTip(item);
                for (const key in data) {
                    this.$set(item, key, data[key]);
                }
            } else {
                console.log("群文字消息=============================>");
                // 这是文字消息
                this.$set(
                    item,
                    "avatar",
                    await this.elememt.getWords(item.from)
                );
            }
        },
        // 处理单聊消息
        async handlerC2C(item) {
            if (item.type === "TIMCustomElem") {
                this.$set(item.payload, "text", item.payload.extension);
            }
        },
        // 处理群内通知
        async handlerGroupTip(item) {
            let operationType = item.payload.operationType;
            let memberRes = await this.getMemberInfo(item.payload.userIDList);
            console.log(
                "去消息处理====================================>",
                memberRes
            );
            let names = memberRes.map((ele) => {
                return ele.nick;
            });
            let handlerData = {
                isSysytem: true,
                sysytemMsg: "",
            };
            switch (operationType) {
                case this.TIM.TYPES.GRP_TIP_MBR_PROFILE_UPDATED:
                    handlerData.sysytemMsg = `"<span style="color: #418FE8;">${names.join(
                        " , "
                    )}</span>" ${
                        item.payload.memberList[0].muteTime > 0
                            ? "已被禁言"
                            : "已被解除禁言"
                    }`;
                    break;
                case this.TIM.TYPES.GRP_TIP_MBR_SET_ADMIN:
                    handlerData.sysytemMsg = `"<span style="color: #418FE8;">${names.join(
                        " , "
                    )}</span>" 被设置为管理员`;
                    break;
                case this.TIM.TYPES.GRP_TIP_MBR_KICKED_OUT:
                    handlerData.sysytemMsg = `"<span style="color: #418FE8;">${names.join(
                        " , "
                    )}</span>" 被移除房间`;
                    break;
                case this.TIM.TYPES.GRP_TIP_MBR_JOIN:
                    handlerData.sysytemMsg = `"<span style="color: #418FE8;">${names.join(
                        " , "
                    )}</span>" 加入房间`;
                    break;
                case this.TIM.TYPES.GRP_TIP_GRP_PROFILE_UPDATED:
                    handlerData.sysytemMsg = `房间公告已更新`;
                    break;
            }
            return handlerData;
        },
        // 获取用户信息
        async getMemberInfo(userIDList) {
            let res = await this.$tim.getUserProfile({
                userIDList,
            });
            return res.data;
        },
        // 判断是否展示收到消息的时间是否展示
        isShowTime(prev, next) {
            if (!next) return false;
            let isShow = false;
            if (next - prev >= 300) {
                isShow = true;
            } else {
                isShow = false;
            }
            return isShow;
        },
        // 文件大小处理
        fileSize(size) {
            let str = `${
                size < 1024
                    ? size + "字节"
                    : size / 1024 < 1024
                    ? Math.round(size / 1024) + "K"
                    : Math.round(size / 1024 / 1024) + "M"
            }`;
            return str;
        },
        // 文件类型处理
        typeHandler(name) {
            let type = name.split(".")[name.split(".").length - 1];
            type = type.toLocaleLowerCase();
            console.log(type);
            let imgUrl = require("@/assets/notype-icon.png");
            if (type === "pdf") {
                imgUrl = require("@/assets/pdf-icon.png");
            } else if (type === "doc" || type === "docx") {
                imgUrl = require("@/assets/word-icon.png");
            } else if (type === "xls" || type === "xlsx") {
                imgUrl = require("@/assets/xlsx-icon.png");
            } else if (type === "apk") {
                imgUrl = require("@/assets/apk-icon.png");
            } else if (type === "exe") {
                imgUrl = require("@/assets/exe-icon.png");
            } else if (type === "txt") {
                imgUrl = require("@/assets/txt-icon.png");
            } else if (type === "zip") {
                imgUrl = require("@/assets/zip-icon.png");
            } else if (type === "rar") {
                imgUrl = require("@/assets/rar-icon.png");
            } else {
                imgUrl = require("@/assets/notype-icon.png");
            }
            return imgUrl;
        },
        // 下载文件到本地
        downLoad(item) {
            this.$asyncLoading(async () => {
                axios({
                    url: item.fileUrl,
                    method: "GET",
                    responseType: "blob",
                }).then((res) => {
                    let data = res.data;
                    console.log(res);
                    if (window.navigator.msSaveBlob) {
                        window.navigator.msSaveBlob(data, item.fileName);
                    } else {
                        let a = document.createElement("a");
                        a.href = window.URL.createObjectURL(data);
                        a.setAttribute("downLoad", item.fileName);
                        a.click();
                    }
                });
            });
        },
        // 滚动到底部
        scrollBottom() {
            let scrollBox = this.$refs.scrollBox;
            let scrollElement = this.$refs.scrollElement;
            if (!scrollElement || scrollElement.length === 0) return;
            this.$nextTick(() => {
                let lastElement = scrollElement[scrollElement.length - 1];
                if (scrollBox.clientHeight < lastElement.offsetTop) {
                    scrollBox.scrollTop +=
                        lastElement.offsetTop - scrollBox.clientHeight + 40;
                }
            });
        },
    },
};
</script>
<style lang="less" scoped>
@import "./web-message.less";
</style>
