// 处理时间格式
export function handlerDate(messageDate, isHandler = false) {
    let mes_date = new Date(messageDate * 1000); // 收到消息的时间
    let mes_y = mes_date.getFullYear();
    let mes_m = mes_date.getMonth() + 1;
    let mes_d = mes_date.getDate();
    let mes_h = mes_date.getHours();
    let mes_f = mes_date.getMinutes();
    let cur_date = new Date(); // 当前时间
    let cur_d = cur_date.getDate();
    let time = "";
    if (!isHandler) {
        if (cur_d === mes_d) {
            time = `${mes_h < 10 ? "0" + mes_h : mes_h}:${
                mes_f < 10 ? "0" + mes_f : mes_f
            }`;
        } else if (cur_d - mes_d === 1) {
            time = `昨天 ${mes_h < 10 ? "0" + mes_h : mes_h}:${
                mes_f < 10 ? "0" + mes_f : mes_f
            }`;
        } else if (cur_d - mes_d === 2) {
            time = `前天 ${mes_h < 10 ? "0" + mes_h : mes_h}:${
                mes_f < 10 ? "0" + mes_f : mes_f
            }`;
        } else {
            time = `${mes_m < 10 ? "0" + mes_m : mes_m}-${
                mes_d < 10 ? "0" + mes_d : mes_d
            }`;
        }
    } else {
        time = `${mes_y}-${mes_m < 10 ? "0" + mes_m : mes_m}-${
            mes_d < 10 ? "0" + mes_d : mes_d
        } ${mes_h < 10 ? "0" + mes_h : mes_h}:${
            mes_f < 10 ? "0" + mes_f : mes_f
        }`;
    }
    return time;
}
