<template>
    <div class="web-member" :style="{ width: !isRightHide ? '280px' : '1px' }">
        <div class="member-list">
            <div class="member-count text-one">
                操作
            </div>
            <div class="member-input">
                <input
                    type="text"
                    placeholder="搜索关键字"
                    v-model="searchValue"
                />
                <el-button
                    type="primary"
                    size="small"
                    icon="el-icon-search"
                    @click="onSearch"
                    >搜索</el-button
                >
            </div>
            <div class="member-handler">
                <el-checkbox
                    v-model="checkAll"
                    :disabled="userInfo.userRole === 'Member'"
                    >全选</el-checkbox
                >
                <el-button
                    size="mini"
                    :disabled="userInfo.userRole === 'Member'"
                    @click="onRemoveMember"
                    >移除</el-button
                >

                <el-button
                    size="mini"
                    :disabled="userInfo.userRole === 'Member'"
                    @click="allJinyan"
                    >{{ isAllJinyan ? "房间解禁" : "房间禁言" }}</el-button
                >
            </div>
            <div class="member-count text-one">
                群成员（{{ memberList.length }}）
            </div>
            <el-tabs v-model="activeName">
                <el-tab-pane
                    v-for="(ele, index) of tabs"
                    :key="index"
                    :label="ele.label"
                    :name="ele.label"
                >
                    <div
                        class="member-item"
                        v-for="item in ele.arr"
                        :key="item.userID"
                    >
                        <div class="check-box">
                            <el-checkbox
                                :disabled="
                                    userInfo.userRole === 'Member' ||
                                        item.nativeRole == 0 ||
                                        item.nativeRole == 1
                                "
                                size="medium"
                                v-model="item.isCheck"
                            />
                        </div>
                        <div class="member-item_info">
                            <div class="info-avatar">
                                <img
                                    :class="[
                                        item.isOnLine === 'Offline' &&
                                            'is-gary',
                                    ]"
                                    :src="item.avatar"
                                />
                            </div>
                            <div class="info-detail">
                                <div
                                    class="info-detail_name"
                                    :style="{
                                        marginBottom:
                                            item.nativeRole == 3 && '10px',
                                    }"
                                >
                                    <el-tooltip
                                        :content="item.name"
                                        placement="top"
                                    >
                                        <div class="detail-name text-one">
                                            {{ item.name }}
                                        </div>
                                    </el-tooltip>
                                    <div
                                        :style="{
                                            color:
                                                item.isOnLine === 'Offline'
                                                    ? '#B8B8B8'
                                                    : '#418fe8',
                                        }"
                                    >
                                        （{{
                                            item.isOnLine === "Offline"
                                                ? "离线"
                                                : "在线"
                                        }}）
                                    </div>
                                </div>
                                <div class="info-detail_role text-one">
                                    {{
                                        item.nativeRole == 0
                                            ? "代理机构"
                                            : item.nativeRole == 1
                                            ? "监管部门"
                                            : item.nativeRole == 2
                                            ? "投标单位"
                                            : ""
                                    }}
                                </div>
                            </div>
                        </div>
                        <div class="member-item_handler">
                            <el-tooltip
                                :content="
                                    userInfo.userRole === 'Member'
                                        ? '您没有该权限'
                                        : item.isForbiddenWords
                                        ? '解除禁言'
                                        : '禁言'
                                "
                                placement="top"
                            >
                                <img
                                    :class="[
                                        userInfo.userRole === 'Member' &&
                                            'is-gary',
                                    ]"
                                    :src="
                                        item.isForbiddenWords
                                            ? require('@/assets/jiejin-icon.png')
                                            : require('@/assets/jinyan-icon.png')
                                    "
                                    @click="isJinyan(item)"
                                />
                            </el-tooltip>
                            <el-tooltip
                                :content="
                                    userInfo.userRole === 'Member'
                                        ? '您没有该权限'
                                        : '发消息'
                                "
                                placement="top"
                            >
                                <img
                                    :class="[
                                        userInfo.userRole === 'Member' &&
                                            'is-gary',
                                    ]"
                                    src="@/assets/message-icon.png"
                                    @click="chat(item)"
                                />
                            </el-tooltip>
                            <el-tooltip
                                :content="
                                    userInfo.userRole === 'Member'
                                        ? '您没有该权限'
                                        : '移除该成员'
                                "
                                placement="top"
                            >
                                <img
                                    @click="remove(item)"
                                    :class="[
                                        userInfo.userRole === 'Member' &&
                                            'is-gary',
                                    ]"
                                    src="@/assets/remove-icon.png"
                                />
                            </el-tooltip>
                        </div>
                    </div>
                </el-tab-pane>
            </el-tabs>
        </div>
        <div
            class="show-or-hide"
            :style="{ right: positionRight + 'px' }"
            @click="
                $store.commit('updateIsRightHide', !$store.state.isRightHide)
            "
        >
            <img
                src="@/assets/arraw-left.png"
                :style="{ transform: !isRightHide ? 'rotate(180deg)' : '' }"
            />
        </div>
    </div>
</template>
<script>
import { mapState } from "vuex";
import { handlerDate } from "@/utils/time";
import axios from "axios";
export default {
    computed: {
        ...mapState({
            isRightHide: (state) => state.isRightHide,
            isLeftHide: (state) => state.isLeftHide,
            userInfo: (state) => state.userInfo,
            checkedConversationInfo: (state) => state.checkedConversationInfo,
            theme: (state) => state.theme,
            clickJinyanBtn: (state) => state.clickJinyanBtn,
        }),
        // 是否全员禁言，是true则代表已全禁，否则为未全禁
        isAllJinyan() {
            let bool = false;
            let arr = this.memberList.filter((ele) => ele.nativeRole == 2);
            if (arr.every((ele) => ele.isForbiddenWords)) {
                bool = true;
            } else {
                bool = false;
            }
            return bool;
        },
        tabs() {
            console.log("重新加载=--------------->");
            let arr1 = [];
            let arr2 = [];
            if (!this.searchValue) {
                this.memberList.forEach((ele) => {
                    if (ele.nativeRole != 3 && ele.name.indexOf("游客") == -1) {
                        arr1.push(ele);
                    } else {
                        console.log("yoke=======================>", ele);
                        arr2.push(ele);
                    }
                });
            } else {
                this.activeName = "开标大厅";
                this.memberList.forEach((ele) => {
                    if (ele.nativeRole != 3) {
                        if (ele.name.indexOf(this.searchValue) !== -1) {
                            arr1.push(ele);
                        }
                    } else {
                        arr2.push(ele);
                    }
                });
            }
            return [
                {
                    label: "开标大厅",
                    arr: arr1,
                },
                {
                    label: "游客",
                    arr: arr2,
                },
            ];
        },
        checkAll: {
            get() {
                let bool = false;
                let arr = [];
                for (const i of this.memberList) {
                    if (i.roleStatus === "Member") {
                        arr.push(i);
                    }
                }
                if (arr.length === 0) {
                    bool = false;
                } else if (arr.every((ele) => ele.isCheck)) {
                    bool = true;
                } else {
                    bool = false;
                }
                return bool;
            },
            set() {
                let bool = this.checkAll;
                for (const i of this.memberList) {
                    if (i.roleStatus === "Member") {
                        this.$set(i, "isCheck", !bool);
                    }
                }
            },
        },
    },
    data() {
        return {
            activeName: "开标大厅",
            name: "member",
            searchValue: "",
            memberList: [],
            positionRight: 0,
            onLineList: [],
        };
    },
    watch: {
        isLeftHide(value) {
            if (!value) {
                this.$store.commit("updateIsRightHide", true);
            }
        },
        isRightHide(vale) {
            if (!vale) {
                this.getMmeberList();
            }
        },
        clickJinyanBtn(value) {
            console.log("外部操作禁言----------------------------------->");
            if (value) {
                console.log(
                    "外部操作禁言----------------------------------->true"
                );
                this.memberFun().then(() => {
                    if (!this.isAllJinyan) {
                        console.log(
                            "船新版本-------------------->",
                            this.memberList
                        );
                        this.allJinyan();
                    }
                    this.$store.commit("updateClickJinyanBtn", false);
                });
            }
        },
    },
    updated() {
        setTimeout(() => {
            this.getScrollBar();
        }, 50);
    },
    methods: {
        // 获取滚动条宽度
        getScrollBar() {
            let con = document.querySelector(".web-message").scrollWidth;
            let msg = document.querySelector(".web-message .message-list")
                .scrollWidth;
            this.positionRight = con - msg;
            console.log(
                "asdasdsa",
                window.innerWidth,
                con,
                msg,
                this.positionRight
            );
        },
        // 获取成员列表
        getMmeberList() {
            this.$asyncLoading(async () => {
                await this.memberFun();
            });
        },
        async memberFun() {
            let memberRes = await this.$tim.getGroupMemberList({
                groupID: this.checkedConversationInfo.userOrGroupId,
                count: 99999,
                offset: 0,
            });
            let To_Account = memberRes.data.memberList.map((ele) => {
                return ele.userID;
            });
            let onLineRes = await this.$apis.isOnline({ To_Account });
            this.onLineList = onLineRes.QueryResult;
            console.log(
                "memberRes.data------------------------------->xxx",
                memberRes.data
            );
            this.memberList = memberRes.data.memberList.map((ele) => {
                return {
                    name: ele.nick,
                    roleStatus: ele.role,
                    role:
                        ele.role === "Owner"
                            ? "代理机构"
                            : ele.role === "Admin"
                            ? "监管部门"
                            : "投标单位",
                    isForbiddenWords:
                        new Date(ele.muteUntil * 1000) > new Date()
                            ? true
                            : false,
                    userID: ele.userID,
                    isOnLine: this.userOnline(ele.userID),
                    avatar: this.handlerAvatar(
                        this.getCostomWord(
                            ele.memberCustomField,
                            "group_member_sex"
                        )
                    ),
                    nativeRole: this.getCostomWord(
                        ele.memberCustomField,
                        "group_memer_role"
                    ),
                    isCheck: false,
                };
            });
            this.$forceUpdate();
            console.log(
                "memberRes.data.memberList----->after",
                this.memberList,
                this.tabs
            );
        },
        // 获取自定义字段
        getCostomWord(arr, key) {
            let str = "";
            for (const i of arr) {
                if (i.key === key) {
                    str = i.value;
                }
            }
            return str || null;
        },
        // 处理头像
        handlerAvatar(num) {
            let str = require("@/assets/man-icon.png");
            if (num === "1") {
                str =
                    this.theme === 0
                        ? require("@/assets/man-icon.png")
                        : require("@/assets/dark-man-icon.png");
            } else if (num === "0") {
                str =
                    this.theme === 0
                        ? require("@/assets/wuman-icon.png")
                        : require("@/assets/dark-wuman-icon.png");
            }
            return str;
        },
        // 查询用户在线状态
        userOnline(userId) {
            let index = this.onLineList.findIndex(
                (ele) => ele.To_Account === userId
            );
            return this.onLineList[index].Status;
        },
        // 全员禁言和解禁
        async allJinyan() {
            if (this.userInfo.userRole === "Member") return;
            this.$asyncLoading(async () => {
                let arr = this.memberList
                    .filter((ele) => ele.nativeRole && ele.nativeRole == 2)
                    .map((ele) => {
                        return ele.userID;
                    });
                console.log(
                    "点击禁言---------------->",
                    "当前数据列表：" + this.memberList,
                    arr
                );
                let res = await this.$apis.memberJinyan({
                    GroupId: this.checkedConversationInfo.userOrGroupId,
                    Members_Account: arr,
                    ShutUpTime: this.isAllJinyan ? 0 : 999999,
                });
                this.$store.commit("updateIsRightHide", true);
                console.log("全员禁言后===============>", res);
            });
        },
        // 禁言与解禁
        isJinyan(item) {
            if (this.userInfo.userRole === "Member") return;
            if (
                item.userID === this.$parent.userInfo.userID ||
                item.role === "监管部门" ||
                item.role === "代理机构"
            )
                return this.$notify({
                    type: "warning",
                    title: "系统消息",
                    message: "您不能对自己、监管部门或代理机构进行禁言操作",
                });
            this.$asyncLoading(async () => {
                let res = await this.$apis.memberJinyan({
                    GroupId: this.checkedConversationInfo.userOrGroupId,
                    Members_Account: [item.userID],
                    ShutUpTime: item.isForbiddenWords ? 0 : 999999,
                });
                console.log("res======================>", res);
                // let index = this.memberList.findIndex(
                //     (ele) => ele.userID === item.userID
                // );
                // this.memberList[index].isForbiddenWords = !this.memberList[
                //     index
                // ].isForbiddenWords;
                this.$forceUpdate();
            });
        },
        // 发送消息
        chat(item) {
            if (this.userInfo.userRole === "Member") return;
            let conversationList = this.$parent.$parent.getChild("conversation")
                .conversations;
            let isExistence = conversationList.find(
                (ele) => ele.userOrGroupId === item.userID
            );
            let data = {};
            if (isExistence) {
                data = isExistence;
            } else {
                data = {
                    conversationID: this.TIM.TYPES.CONV_C2C + item.userID,
                    type: this.TIM.TYPES.CONV_C2C,
                    userOrGroupId: item.userID,
                    unreadCount: 0,
                    content: "",
                    time: new Date().getTime(),
                    name: item.name,
                    selfInfo: null,
                    avatar: item.avatar,
                };
                this.$parent.$parent
                    .getChild("conversation")
                    .conversations.unshift(data);
            }
            this.$store.commit("updateCheckedConversationInfo", data);
            this.$store.commit("updateIsRightHide", true);
        },
        // 成员移除
        remove(item) {
            if (this.userInfo.userRole === "Member") return;
            if (
                item.userID === this.$parent.userInfo.userID ||
                item.role === "监管部门" ||
                item.role === "代理机构"
            )
                return this.$notify({
                    type: "warning",
                    title: "系统消息",
                    message: "您不能移除自己、监管部门或代理机构",
                });
            this.$confirm(`您是否要移除成员：${item.name}?`, "提示", {
                confirmButtonText: "移除",
                cancelButtonText: "取消",
                type: "warning",
            })
                .then(() => {
                    this.$tim
                        .deleteGroupMember({
                            groupID: this.checkedConversationInfo.userOrGroupId,
                            userIDList: [item.userID],
                            reason:
                                "您已被移出项目名称为" +
                                this.checkedConversationInfo.name +
                                "的项目",
                        })
                        .then((res) => {
                            let index = this.memberList.findIndex(
                                (ele) => ele.userID === item.userID
                            );
                            this.memberList.splice(index, 1);
                            axios
                                .get("/onlineChat/kickBidder", {
                                    params: { userId: item.userID },
                                })
                                .then((res) => {
                                    console.log(
                                        "踢出成功===================>",
                                        res
                                    );
                                });
                            this.$notify({
                                type: "success",
                                title: "系统消息",
                                message: `您已将${item.name}移除该群`,
                            });
                        });
                })
                .catch(() => {
                    console.log("取消了");
                });
        },
        // 批量删除
        onRemoveMember() {
            console.log("asds");
            let arr = [];
            this.memberList.forEach((ele) => {
                if (ele.roleStatus == "Member" && ele.isCheck) {
                    arr.push(ele);
                }
            });
            console.log(arr);
            if (arr.length === 0) {
                this.$notify({
                    type: "info",
                    title: "系统消息",
                    message: "请选择要移除的群成员",
                });
            } else {
                this.$confirm(
                    `您是否要移除选中的${arr.length}个成员?`,
                    "提示",
                    {
                        confirmButtonText: "移除",
                        cancelButtonText: "取消",
                        type: "warning",
                    }
                )
                    .then(() => {
                        for (const ele of arr) {
                            this.$tim
                                .deleteGroupMember({
                                    groupID: this.checkedConversationInfo
                                        .userOrGroupId,
                                    userIDList: arr.map((ele) => {
                                        return ele.userID;
                                    }),
                                    reason:
                                        "您已被移出项目名称为" +
                                        this.checkedConversationInfo.name +
                                        "的项目",
                                })
                                .then((res) => {
                                    console.log(res);
                                    let num = 0;
                                    for (const ele of res.data.userIDList) {
                                        let index = this.memberList.findIndex(
                                            (i) => i.userID === ele
                                        );
                                        this.memberList.splice(index, 1);
                                        axios
                                            .get("/onlineChat/kickBidder", {
                                                params: { userId: ele },
                                            })
                                            .then((res) => {
                                                num++;
                                                console.log(
                                                    "踢出成功===================>",
                                                    res,
                                                    num
                                                );
                                                if (
                                                    num ===
                                                    res.data.userIDList.length
                                                ) {
                                                    this.$notify({
                                                        type: "success",
                                                        title: "系统消息",
                                                        message: `成功移除${res.data.userIDList.length}个成员`,
                                                    });
                                                }
                                            });
                                    }
                                });
                        }
                    })
                    .catch(() => {
                        console.log("取消了");
                    });
            }
        },
        // 搜索开标大厅
        onSearch() {
            if (!this.searchValue)
                return this.$notify({
                    type: "warning",
                    title: "系统消息",
                    message: "请输入搜索关键词",
                });
        },
    },
};
</script>
<style lang="less" scoped>
@import "./web-member.less";
</style>
