<template>
    <transition name="show">
        <div v-show="trtcInfo.isShowComponent">
            <div
                v-show="
                    trtcInfo.roomIdAndType &&
                        trtcInfo.roomIdAndType.type === 'audio'
                "
                class="web-trtc background-color"
            >
                <div class="web-trtc_label">
                    <div class="label-big">
                        {{
                            trtcInfo.type === "ANSWERED"
                                ? "通话中"
                                : trtcInfo.type === "WITH_ANSWER"
                                ? "您发起了语音通话"
                                : trtcInfo.type === "CALLING"
                                ? "您收到了语音邀请"
                                : ""
                        }}
                    </div>
                    <div class="label-sub">{{ trtcInfo.subTitle }}</div>
                </div>
                <div class="web-trtc_btn">
                    <el-button size="small" type="danger" @click="close"
                        >挂断</el-button
                    >
                    <el-button
                        size="small"
                        type="primary"
                        v-show="trtcInfo.type === 'CALLING'"
                        @click="join"
                        >接听</el-button
                    >
                </div>
                <div id="audio"></div>
            </div>
            <div
                class="video"
                v-show="
                    trtcInfo.roomIdAndType &&
                        trtcInfo.roomIdAndType.type === 'video'
                "
                :class="isMax ? 'video-max' : 'video-min'"
            >
                <div
                    class="video-box"
                    v-show="
                        trtcInfo.type === 'ANSWERED' &&
                            trtcInfo.otherPartyCamera
                    "
                    id="video"
                    ref="video"
                ></div>
                <div
                    class="video-box duifang-no-video"
                    v-show="trtcInfo.type !== 'ANSWERED'"
                >
                    等待对方接听···
                </div>
                <div
                    class="video-box duifang-no-video"
                    v-show="
                        trtcInfo.type === 'ANSWERED' &&
                            !trtcInfo.otherPartyCamera
                    "
                >
                    对方没有摄像头
                </div>
                <div class="web-trtc_btn video-btn">
                    <el-button size="small" type="danger" @click="close"
                        >挂断</el-button
                    >
                    <el-button
                        size="small"
                        type="primary"
                        v-show="trtcInfo.type === 'CALLING'"
                        @click="join"
                        >接听</el-button
                    >
                </div>
                <div class="video-close">
                    <img
                        @click="isMax = !isMax"
                        :src="
                            isMax
                                ? require('@/assets/min-icon.png')
                                : require('@/assets/max-icon.png')
                        "
                    />
                </div>
                <div
                    id="nativeVideo"
                    ref="nativeVideo"
                    class="native-video"
                    v-show="trtcInfo.isCamera"
                ></div>
                <div v-show="!trtcInfo.isCamera" class="native-video no-video">
                    您没有摄像头
                </div>
            </div>
        </div>
    </transition>
</template>
<script>
import { mapState } from "vuex";
export default {
    computed: {
        ...mapState({
            userInfo: (state) => state.userInfo,
            trtcInfo: (state) => state.trtcInfo,
            checkedConversationInfo: (state) => state.checkedConversationInfo,
            theme: (state) => state.theme,
        }),
    },
    data() {
        return {
            name: "trtc",
            isMax: false,
        };
    },
    methods: {
        // 初始化状态
        initStatus() {
            this.$nextTick(async () => {
                console.log(
                    "trtc组件创建成功====================================>"
                );
                // 检测当前环境是否支持实时音视频
                let testRes = await this.trtcInfo.TRTC.checkSystemRequirements();
                // 检查当前设备是否支持音视频
                // 检测麦克风
                let micropRes = await this.trtcInfo.TRTC.getMicrophones();
                // 检查摄像头
                let cameraRes = await this.trtcInfo.TRTC.getCameras();

                console.log(
                    testRes,
                    micropRes,
                    cameraRes,
                    "***********************************testRes********************************"
                );
                // 创建音视频对象
                this.$store.dispatch("createTrtcObj");
                this.$store.commit("updateTrtc", {
                    browserSupport: testRes.result,
                    isMicrophone: micropRes.length === 0 ? false : true,
                    isCamera: cameraRes.length === 0 ? false : true,
                });
                console.log(
                    "'***********************************testRes********************************'",
                    this.trtcInfo
                );
                // 远端流订阅成功监听事件
                this.trtcInfo.trtcClient.on("stream-subscribed", (event) => {
                    console.log(
                        "远端流订阅成功========================>",
                        event
                    );
                    const remoteStream = event.stream;
                    remoteStream.play(this.trtcInfo.roomIdAndType.type);
                    this.$store.commit("updateTrtc", {
                        type: "ANSWERED",
                        subTitle: `正在和${this.trtcInfo.roomIdAndType.name}通话`,
                    });
                });
                // 远端流添加监听事件
                this.trtcInfo.trtcClient.on("stream-added", (event) => {
                    console.log(
                        "远端流添加成功==========================>",
                        event
                    );
                    const remoteStream = event.stream;
                    this.trtcInfo.trtcClient.subscribe(remoteStream);
                });
                // 远端流移除监听事件
                this.trtcInfo.trtcClient.on("stream-removed", (event) => {
                    console.log(
                        "远端用户已取消本次通话======================>",
                        event
                    );
                    const remoteStream = event.stream;
                    this.$refs.nativeVideo.innerHTML = "";
                    this.$refs.video.innerHTML = "";
                });
                this.trtcInfo.trtcClient.on("peer-join", (event) => {
                    console.log(
                        "peer-join远端进房了===================================>",
                        event,
                        this.userInfo
                    );
                });
                this.trtcInfo.trtcClient.on("peer-leave", (event) => {
                    console.log(
                        "peer-leave远端退房了===================================>",
                        event,
                        this.userInfo
                    );
                });
            });
        },
        // 初始化本地流
        join() {
            if (
                this.trtcInfo.roomIdAndType.type === "video" &&
                !this.trtcInfo.isCamera
            ) {
                this.sendCustomMessage(
                    this.trtcInfo.roomIdAndType.id,
                    "没有摄像头",
                    "NO_CAMERA"
                );
            }
            console.log(
                "this.trtcInfo.roomIdAndType收到消息点击加入房间时初始化本地流====================》",
                this.trtcInfo.roomIdAndType
            );
            let type = this.trtcInfo.roomIdAndType.type;
            this.$store.dispatch("createNative", type);

            console.log(
                "this.trtcInfo.roomIdAndType收到消息点击加入房间时初始化本地流完成====================》",
                this.trtcInfo.localStream
            );
            if (type === "audio") {
                this.trtcInfo.localStream.setAudioProfile("high");
            } else {
                this.trtcInfo.localStream.setVideoProfile("4K");
            }
            this.$asyncLoading(async () => {
                this.$store.dispatch("joinRoom").then(() => {
                    console.log(
                        "==================================>加入房间成功、本地流初始化成功、发布本地流成功"
                    );
                    this.$store.commit("updateTrtc", {
                        isShowComponent: true,
                        type: "ANSWERED",
                        subTitle: `正在和${this.trtcInfo.roomIdAndType.name}通话`,
                    });
                });
            });
        },
        close() {
            this.isMax = false;
            let type = "";
            let msg = "";
            console.log(
                this.trtcInfo.roomIdAndType,
                "this.trtcInfo.roomIdAndType=====================》？"
            );
            // 如果本地流存在，说明是发起者取消了，则先取消本地流发布在发送消息，如不存在则说明是接收者拒接了，则直接发送消息
            if (this.trtcInfo.localStream) {
                this.trtcInfo.trtcClient.unpublish(this.trtcInfo.localStream);
                this.trtcInfo.trtcClient.leave();
                if (this.trtcInfo.type === "ANSWERED") {
                    type = "CHAT_END";
                    msg = "已结束通话";
                } else {
                    type = "CONCEL";
                    msg = "已取消";
                }
            } else {
                type = "REFUSE_TO_ACCEPT";
                msg = "已拒接";
            }
            this.sendCustomMessage(this.trtcInfo.roomIdAndType.id, msg, type);
            this.$store.commit("updateTrtc", {
                localStream: null,
                isShowComponent: false,
                type: "",
                subTitle: "",
            });
            this.$refs.nativeVideo.innerHTML = "";
            this.$refs.video.innerHTML = "";
        },
        // 发送自定义消息
        /**
         * @param { toId } 对方ID
         * @param { msg } 消息内容
         * @param { type } 状态类型
         */
        async sendCustomMessage(toId, msg, type) {
            let message = this.$tim.createCustomMessage({
                to: toId,
                conversationType: this.TIM.TYPES.CONV_C2C,
                payload: {
                    data: type,
                    description: "",
                    extension: msg,
                },
            });
            let sendRes = await this.$tim.sendMessage(message);
            if (this.$parent.getChild("message")) {
                let userRes = await this.$parent
                    .getChild("message")
                    .getMemberInfo([sendRes.data.message.from]);
                sendRes.data.message.nick = userRes.data.nick;
                sendRes.data.message.avatar =
                    userRes.data.profileCustomField[0].value === "0"
                        ? this.theme === 0
                            ? require("@/assets/wuman-icon.png")
                            : require("@/assets/dark-wuman-icon.png")
                        : this.theme === 0
                        ? require("@/assets/man-icon.png")
                        : require("@/assets/dark-man-icon.png");
                this.$set(
                    sendRes.data.message.payload,
                    "text",
                    sendRes.data.message.payload.extension
                );
                this.$parent.getChild(
                    "message"
                ).messageList = this.$parent
                    .getChild("message")
                    .messageList.concat([sendRes.data.message]);
            }
        },
        /**
         * 处理收到关于音视频自定义消息的内容
         */
        async getTrtcInfo(item) {
            console.log(
                "收到了来自音视频相关的消息===========================>",
                item
            );
            let status = item.payload.data;
            let userRes = await this.$parent.getMemberInfo([item.from]);
            if (status === "AUDIO_OR_VIDEO") {
                let data = JSON.parse(item.payload.description);
                console.log(
                    "收到语音通话或视频通话=========================>",
                    data
                );
                this.$store.commit("updateTrtc", {
                    roomIdAndType: data,
                    isShowComponent: true,
                    type: "CALLING",
                    subTitle: `${userRes.nick}对您发起了语音通话`,
                });
            } else if (status === "REFUSE_TO_ACCEPT" || status === "CHAT_END") {
                console.log("对方拒接或通话结束=========================>");
                this.trtcInfo.trtcClient.unpublish(this.trtcInfo.localStream);
                this.trtcInfo.trtcClient.leave();
                this.$store.commit("updateTrtc", {
                    localStream: null,
                    isShowComponent: false,
                    type: "",
                    subTitle: "",
                });
            } else if (status === "CONCEL") {
                console.log("发起者取消=========================>");
                this.$store.commit("updateTrtc", {
                    localStream: null,
                    isShowComponent: false,
                    type: "",
                    subTitle: "",
                });
            } else if (status === "NO_CAMERA") {
                this.$store.commit("updateTrtc", {
                    otherPartyCamera: false,
                });
            }
        },
    },
};
</script>
<style lang="less" scoped>
@import "./web-trtc.less";
</style>
