<template>
    <div
        :class="[
            'web-chat',
            theme === 0 ? 'light-theme' : theme === 1 ? 'dark-theme' : '',
        ]"
    >
        <web-conversation v-show="userInfo && userInfo.userRoleNum !== 3" />
        <component
            :style="{
                width: userInfo
                    ? userInfo.userRoleNum === 3
                        ? '100%'
                        : !isLeftHide
                        ? '70%'
                        : 'calc(100% - 88px)'
                    : '100%',
            }"
            :is="checkedConversationInfo ? 'webMessage' : 'webPlaceholder'"
        />
        <web-trtc />
        <transition name="fade">
            <div class="top-tip" v-if="showTip">
                <el-alert
                    class="top-tip_box"
                    title="群公告提示"
                    type="info"
                    :description="groupMsg"
                    show-icon
                ></el-alert>
            </div>
        </transition>
        <web-pdf />
    </div>
</template>
<script>
import { mapState } from "vuex";
import SDK_INFO from "@/utils/userSig";
// 引入组件
import webConversation from "@/components/web-conversation/web-conversation";
import webMessage from "@/components/web-message/web-message";
import webPlaceholder from "@/components/web-placeholder/web-placeholder";
import webTrtc from "@/components/web-trtc/web-trtc";
import webPdf from "@/components/web-pdf/web-pdf";
import axios from "axios";
export default {
    components: {
        webConversation,
        webMessage,
        webPlaceholder,
        webTrtc,
        webPdf,
    },
    // vuex中的公用数据
    computed: {
        ...mapState({
            userInfo: (state) => state.userInfo,
            isLeftHide: (state) => state.isLeftHide,
            checkedConversationInfo: (state) => state.checkedConversationInfo,
            localStream: (state) => state.localStream,
            trtcClient: (state) => state.trtcClient,
            theme: (state) => state.theme,
            outImportMessage: (state) => state.outImportMessage,
            isRightHide: (state)=>state.isRightHide,
        }),
    },
    watch: {
        checkedConversationInfo: {
            handler(val) {
                console.log(
                    "初始化时收到消息------------>",
                    val,
                    this.listenType
                );
                if (
                    val &&
                    val.conversationID &&
                    this.listenType !== "WINDOW_MIN"
                ) {
                    this.udpateUnReadMessage();
                }
            },
            deep: true,
            immediate: true,
        },
    },
    data() {
        return {
            listenType: "WINDOW_RESET", // 父级界面初始化后该串口的状态：WINDOW_MAX--窗口最大化，WINDOW_MIN--窗口最小化，WINDOW_RESET--窗口还原
            showTip: false,
            groupMsg: "",
            importData: {},
        };
    },
    mounted() {
        this.parentListen();
        console.log(
            "判断时导出还是正常初始化--------------------------------------->",
            this.outImportMessage
        );
        setTimeout(() => {
            if (this.outImportMessage) {
                console.log(
                    "列表出点击导出消息--------------------------------------->",
                    this.importData
                );
                let data = {
                    userID: String(this.importData.userId),
                    userName: this.importData.userName,
                    roomId: String(this.importData.roomId),
                    roomName: this.importData.roomName,
                    userSex: Number(this.importData.sex),
                    userRoleNum: Number(this.importData.role),
                    kickStatus: Number(this.importData.kickStatus),
                    userRole:
                        this.importData.role === "0"
                            ? "Owner"
                            : this.importData.role === "1"
                            ? "Admin"
                            : "Member",
                };
                this.init(data);
            } else {
                console.log(
                    "正常初始化----------------------------------------------->"
                );
                axios
                    .get("/onlineChat/getUser")
                    .then((res) => {
                        console.log(
                            "/onlineChat/getUser======>请求成功",
                            res.data
                        );
                        let resData = res.data;
                        let data = {
                            userID: String(resData.userId),
                            userName: resData.userName,
                            roomId: String(resData.roomId),
                            roomName: resData.roomName,
                            userSex: Number(resData.sex),
                            userRoleNum: Number(resData.role),
                            kickStatus: Number(resData.kickStatus),
                            userRole:
                                resData.role == "0"
                                    ? "Owner"
                                    : resData.role == "1"
                                    ? "Admin"
                                    : "Member",
                        };
                        this.init(data);
                    })
                    .catch((err) => {
                        console.log("/onlineChat/getUser======>请求失败", err);
                        this.$notify({
                            type: "error",
                            title: "请求失败",
                            message: err,
                        });
                        if (this.$route.query) {
                            let data = {
                                userID: this.$route.query.userID,
                                userName: this.$route.query.userName,
                                roomId: this.$route.query.roomId,
                                roomName: this.$route.query.roomName,
                                userSex: Number(this.$route.query.sex),
                                userRoleNum: Number(this.$route.query.role),
                                kickStatus: Number(
                                    this.$route.query.kickStatus
                                ),
                                userRole:
                                    Number(this.$route.query.role) === 0
                                        ? "Owner"
                                        : Number(this.$route.query.role) === 1
                                        ? "Admin"
                                        : "Member",
                            };
                            this.init(data);
                        }
                    });
            }
        }, 3000);
    },
    destroyed() {
        this.$tim.logout();
    },
    methods: {
        // 初始化
        async init(data) {
            console.log("数据处理后的data对象========================》", data);
            this.$store.commit("updateUserInfo", data);
            this.$asyncLoading(async () => {
                // 查询此用户是否被导入
                let selectRes = await this.$apis.seleceUserImport({
                    CheckItem: [{ UserID: this.userInfo.userID }],
                });
                console.log("查询此用户是否被导入====>", selectRes);
                // 判断是否已导入了该用户
                if (selectRes.ResultItem[0].AccountStatus !== "Imported") {
                    // 未导入则先导入
                    this.$apis
                        .importUser({
                            Identifier: this.userInfo.userID,
                            Nick: this.userInfo.userName,
                        })
                        .then((res) => {
                            console.log("res==================》导入成功", res);
                            // 导入后设置自定义字
                        });
                }
                // 登录IM
                this.$tim
                    .login({
                        userID: this.userInfo.userID,
                        userSig: SDK_INFO.getUserSig(this.userInfo.userID),
                    })
                    .then(() => {
                        this.$notify({
                            title: "welcome",
                            type: "success",
                            dangerouslyUseHTMLString: true,
                            message: "初始化成功",
                            duration: 3000,
                        });
                        this.getChild("trtc").initStatus();
                        // 登录成功后初始化获取会话列表,获取时机在SDK准备完成后
                        this.$tim.on(
                            this.TIM.EVENT.SDK_READY,
                            this.sdkReadyAfter
                        );
                        // 收到消息后的回调事件
                        this.$tim.on(
                            this.TIM.EVENT.MESSAGE_RECEIVED,
                            this.messageReceived
                        );
                        // 被踢下线监听
                        this.$tim.on(this.TIM.EVENT.KICKED_OUT, this.kickedOut);
                    });
            });
        },
        // 发送一条消息
        async sendOneMsg() {
            if (this.userInfo.userRoleNum != 3) {
                let message = this.$tim.createTextMessage({
                    to: this.userInfo.roomId,
                    conversationType: this.TIM.TYPES.CONV_GROUP,
                    payload: {
                        text: `大家好，我是${this.userInfo.userName}`,
                    },
                });
                console.log("创建的群消息=======================》", message);
                let res = await this.$tim.sendMessage(message);
            }
        },
        // 设置自定义字段
        setWords() {
            this.$tim
                .setGroupMemberCustomField({
                    groupID: this.userInfo.roomId,
                    memberCustomField: [
                        {
                            key: "group_member_sex",
                            value: this.userInfo.userSex + "",
                        },
                        {
                            key: "group_memer_role",
                            value: this.userInfo.userRoleNum + "",
                        },
                    ],
                })
                .then((res) => {
                    console.log(res, "设置成功============================>");
                });
        },
        // 查询群是否存在
        async selectGroup(id) {
            let resData = await this.$tim.searchGroupByID(id).catch((err) => {
                console.log(err);
            });
            console.log(resData);
            return resData;
        },
        // sdk准备完成的回调函数
        async sdkReadyAfter() {
            console.log('修改自定义用户信息---------------------------->',this.userInfo)
            // 修改自定义用户信息
            this.$tim
                .updateMyProfile({
                    profileCustomField: [
                        {
                            key: "Tag_Profile_Custom_sex",
                            value: this.userInfo.userSex + "",
                        },
                    ],
                })
                .then((res) => {
                    console.log("修改成功-----------------updateMyProfile========================》", res);
                }).catch(()=>{
                    console.log("修改失败-----------------updateMyProfile========================》");
                });
            // 查询群是否存在
            let res = await this.selectGroup(this.userInfo.roomId).catch(() => {
                window.parent.postMessage({ importList: null }, "*");
            });
            if (!res) {
                console.log("群组不存在，请先创建", this.userInfo);
                if (this.userInfo.userRole === "Owner") {
                    this.$tim
                        .createGroup({
                            type: this.TIM.TYPES.GRP_PUBLIC,
                            name: String(parseInt(Math.random() * 1000000)),
                            groupID: this.userInfo.roomId,
                            joinOption: this.TIM.TYPES.JOIN_OPTIONS_FREE_ACCESS,
                        })
                        .then(() => {
                            this.sendOneMsg().then(() => {
                                console.log(
                                    "消息发送成======================================================>"
                                );
                            });
                            this.setWords();
                        });
                }
            } else {
                console.log("此群已存在");
                // 查询用户是否已加群
                let isJoinRes = await this.$tim
                    .getGroupMemberProfile({
                        groupID: this.userInfo.roomId,
                        userIDList: [this.userInfo.userID],
                    })
                    .catch((err) => {
                        console.log(err);
                        return null;
                    });
                // 若不存在则加入
                if (!isJoinRes) {
                    console.log(
                        "用户未加群===================================>"
                    );
                    console.log(
                        "用户是否被踢出================>",
                        this.userInfo.kickStatus !== 1
                    );
                    // 查询是否已经被踢，如已被踢则不加
                    if (this.userInfo.kickStatus !== 1) {
                        console.log(
                            "未被踢====================================>"
                        );
                        this.$tim
                            .joinGroup({
                                groupID: this.userInfo.roomId,
                                type: this.TIM.TYPES.GRP_PUBLIC,
                            })
                            .then(() => {
                                console.log(
                                    "加群成功====================================>",
                                    this.userInfo
                                );
                                // 设置群成员自定义字段
                                this.setWords();
                                // 发送一条消息，避免加群后刷新页面会话丢失
                                this.sendOneMsg().then(() => {
                                    console.log(
                                        "消息发送成功====================================================>"
                                    );
                                    // 加入成功后判断是否需要设置为管理员还是禁言
                                    if (this.userInfo.userRole == 'Admin') {
                                        console.log(
                                            "该用户是监管部门，需要设置为管理员===========================>",
                                            this.userInfo
                                        );
                                        this.$apis.setGroupAdmin({
                                            GroupId: this.userInfo.roomId,
                                            Member_Account: this.userInfo
                                                .userID,
                                            Role: this.userInfo.userRole,
                                        });
                                    }
                                    // 群成员为投标单位才禁言，游客不进行任何操作
                                    else if (
                                        this.userInfo.userRoleNum == 2
                                    ) {
                                        localStorage.setItem(
                                            "areUsersForbiddenToSpeak",
                                            false
                                        );
                                        console.log(
                                            "该用户是投标单位，需要设置为禁言===========================>",
                                            this.userInfo
                                        );
                                        this.$apis
                                            .memberJinyan({
                                                GroupId: this.userInfo.roomId,
                                                Members_Account: [
                                                    this.userInfo.userID,
                                                ],
                                                ShutUpTime: 999999,
                                            })
                                            .then((res) => {
                                                localStorage.setItem(
                                                    "areUsersForbiddenToSpeak",
                                                    true
                                                );
                                                console.log(
                                                    "禁言成功=======================>",
                                                    res
                                                );
                                            });
                                    }
                                });
                            });
                    } else {
                        console.log(
                            "已被踢====================================>"
                        );
                    }
                } else {
                    console.log("用户已加群========================》");
                    // 重新设置群成员自定义字段
                    this.selectJinyan();
                    this.setWords();
                }
            }
            this.getChild("conversation").getConversationList();
        },
        // 被踢下线
        kickedOut(event) {
            let type = event.data.type;
            this.getChild("conversation").conversations = [];
            this.$store.commit("updateCheckedConversationInfo", null);
            if (type === this.TIM.TYPES.KICKED_OUT_USERSIG_EXPIRED) {
                this.$notify({
                    title: "系统消息",
                    type: "error",
                    dangerouslyUseHTMLString: true,
                    message: "<strong>登录失效，请重新登录</strong>",
                    duration: 3000,
                });
            } else {
                this.$notify({
                    title: "系统消息",
                    type: "error",
                    dangerouslyUseHTMLString: true,
                    message: "<strong>您已在另一台设备上登录</strong>",
                    duration: 3000,
                });
            }
        },
        // 收到消息的回调函数
        async messageReceived(event) {
            let msgRes = event.data[0];
            console.log("msgRes=========================>收到了消息", msgRes);
            this.getChild("conversation")
                .getConversationList()
                .then(() => {
                    this.sendMsgToParent(this.listenType, msgRes);
                });
            if (
                msgRes.payload.operationType === 4 &&
                this.userInfo.userRole === "Member"
            ) {
                this.$store.commit("updateCheckedConversationInfo", null);
                this.$notify({
                    title: "系统消息",
                    type: "info",
                    message: msgRes.payload.handleMessage,
                });
                axios.get("/onlineChat/getUser").then((res) => {
                    console.log(
                        "/onlineChat/getUser======>请求成功-----被踢之后重新获取数据================》",
                        res.data
                    );
                    let resData = res.data;
                    let data = {
                        userID: String(resData.userId),
                        userName: resData.userName,
                        roomId: String(resData.roomId),
                        roomName: resData.roomName,
                        userSex: Number(resData.sex),
                        userRoleNum: Number(resData.role),
                        kickStatus: Number(resData.kickStatus),
                        userRole:
                            resData.role == "0"
                                ? "Owner"
                                : resData.role == "1"
                                ? "Admin"
                                : "Member",
                    };
                    this.$store.commit("updateUserInfo", data);
                });
            }
            if(msgRes.payload.operationType === 7 && !this.isRightHide) {
                console.log('需要重新加载群成员列表',this.getChild('message').$children)
                let ele = this.getChild('message').$children.find(ele=>ele.name == "member")
                console.log('ele',ele)
                ele.memberFun()
            }
            if (
                msgRes.conversationType === "GROUP" &&
                msgRes.payload.operationType === 6
            ) {
                this.showTip = true;
                this.groupMsg = msgRes.payload.newGroupProfile.introduction;
            }
            if (
                msgRes.conversationType === "GROUP" &&
                msgRes.payload.operationType === 1
            ) {
                for (const i of this.getChild("message").$children) {
                    if (i.name == "member") {
                        i.memberFun();
                    }
                }
            }
            if (
                msgRes.type === "TIMCustomElem" &&
                msgRes.conversationType === "C2C"
            ) {
                this.getChild("trtc").getTrtcInfo(msgRes);
            }
            if (
                this.checkedConversationInfo &&
                this.checkedConversationInfo.conversationID ===
                    msgRes.conversationID
            ) {
                console.log(
                    "当前选中会话ID和收到的会话ID相同------------------>",
                    "checkID:" + this.checkedConversationInfo.conversationID,
                    "getID:" + msgRes.conversationID
                );
                if (msgRes.payload.operationType === 4) {
                    this.$store.commit("updateCheckedConversationInfo", null);
                }
                let avaratUrl = await this.getChild("conversation").getWords(
                    msgRes.from
                );
                console.log(
                    "=================================>avaratUrl",
                    avaratUrl
                );
                this.$set(msgRes, "avatar", avaratUrl);
                if (msgRes.conversationType !== this.TIM.TYPES.CONV_C2C) {
                    this.getChild("message").handlerGROUP(msgRes);
                    if (
                        msgRes.payload.operationType ===
                        this.TIM.TYPES.GRP_TIP_MBR_PROFILE_UPDATED
                    ) {
                        this.getChild("message").$children[1].handlerJinyan();
                    }
                } else {
                    this.getChild("message").handlerC2C(msgRes);
                }
                this.getChild("message").messageList = this.getChild(
                    "message"
                ).messageList.concat([msgRes]);
                console.log(
                    "收到一条消息==================================================>",
                    msgRes,
                    this.getChild("message").messageList
                );
            }
        },
        // 每一秒查询用户是否被禁言，若未被禁言则禁言
        async selectJinyan() {
            let areUsersForbiddenToSpeak = localStorage.getItem(
                "areUsersForbiddenToSpeak"
            );
            if (areUsersForbiddenToSpeak) return;
            let self = this;
            let timer = setInterval(function() {
                // 先查询用户是否加群，若未加群则关闭顶事情
                self.$tim
                    .getGroupMemberProfile({
                        groupID: self.userInfo.roomId,
                        userIDList: [self.userInfo.userID],
                    })
                    .then(async (res) => {
                        console.log(
                            "用户加群循环中========================================================***********************************************************"
                        );
                        // 已加群则查询用户是否需要被禁言
                        // 判断用户身份，若是投标单位则判断是否需要禁言
                        if (self.userInfo.userRoleNum == 2) {
                            // 查询禁言列表
                            let arrRes = await self.$apis.selectJinyan({
                                GroupId: self.userInfo.roomId,
                            });
                            let valueArr = arrRes.ShuttedUinList;
                            let index = valueArr.findIndex(
                                (ele) =>
                                    ele.Member_Account === self.userInfo.userID
                            );
                            console.log(index);
                            if (index != -1) {
                                console.log(
                                    "成员已禁言=====================================*********************************************"
                                );
                                // index不等于-1说明已被禁言，则清除定时器
                                clearInterval(timer);
                            } else {
                                console.log(
                                    "成员未禁言，执行禁言=====================================*********************************************"
                                );
                                // 禁言操作
                                self.$apis
                                    .memberJinyan({
                                        GroupId: self.userInfo.roomId,
                                        Members_Account: [self.userInfo.userID],
                                        ShutUpTime: 999999,
                                    })
                                    .then((res) => {
                                        clearInterval(timer);
                                        localStorage.setItem(
                                            "areUsersForbiddenToSpeak",
                                            true
                                        );
                                    });
                            }
                        } else {
                            clearInterval(timer);
                        }
                    })
                    .catch((err) => {
                        clearInterval(timer);
                    });
            }, 1000);
        },
        // 获取子组件
        getChild(childName) {
            let childs = this.$children;
            let child = null;
            for (const item of childs) {
                if (item.name === childName) {
                    child = item;
                }
            }
            return child;
        },
        // 未读消息上报
        async udpateUnReadMessage() {
            if (
                !this.checkedConversationInfo ||
                !this.checkedConversationInfo.conversationID
            )
                return;
            let res = await this.$tim.setMessageRead({
                conversationID: this.checkedConversationInfo.conversationID,
            });
            this.getChild("conversation").conversations.forEach((ele) => {
                if (
                    ele.conversationID ===
                    this.checkedConversationInfo.conversationID
                ) {
                    this.$set(ele, "unreadCount", 0);
                }
            });
        },
        async getMemberInfo(userIDList) {
            let res = await this.$tim.getUserProfile({
                userIDList,
            });
            return res.data[0];
        },
        // 父级页面变化监听
        parentListen() {
            window.addEventListener("message", async (e) => {
                // this.listenType = e.data;
                console.log(".data========================>", e.data);
                if (e.data.listenType) {
                    console.log(
                        "监听到了窗口变化---------------->",
                        this.listenType
                    );
                    this.listenType = e.data.listenType;
                    if (this.listenType !== "WINDOW_MIN") {
                        console.log(
                            "窗口非最小化---------------->",
                            this.listenType
                        );
                        this.sendMsgToParent(this.listenType, {});
                    } else {
                        console.log(
                            "窗口最小化---------------->",
                            this.listenType
                        );
                    }
                }
                if (e.data.linkType) {
                    this.$tim.updateGroupProfile({
                        groupID: this.userInfo.roomId,
                        introduction: e.data.linkType,
                    });
                }
                if (e.data.msgType) {
                    console.log('e.data.msgType------------------->',e.data)
                    let message = this.$tim.createTextMessage({
                        to: this.userInfo.roomId,
                        conversationType: "GROUP",
                        payload: {
                            text: e.data.msgType,
                        },
                    });
                    let res = await this.$tim.sendMessage(message);
                    if (
                        this.checkedConversationInfo &&
                        this.checkedConversationInfo.userOrGroupId ===
                            this.userInfo.roomId
                    ) {
                        let userRes = await this.getMemberInfo([
                            res.data.message.from,
                        ]);
                        res.data.message.nick = userRes.data.nick;
                        res.data.message.avatar =
                            userRes.data.profileCustomField[0].value === "1"
                                ? require("@/assets/man-icon.png")
                                : require("@/assets/wuman-icon.png");
                        this.getChild("message").messageList = this.getChild(
                            "message"
                        ).messageList.concat([res.data.message]);
                    }
                }
                if (e.data.themeType) {
                    this.$store.commit("updateTheme", e.data.themeType);
                }
                if (e.data.forbiddenData) {
                    console.log(
                        "外部禁言与解禁操作--------------------------------->",
                        this.checkedConversationInfo,
                        this.userInfo.kickStatus
                    );
                    if (this.userInfo.kickStatus == 1) {
                        console.log(
                            "已被移除群聊------------------------------>"
                        );
                        window.parent.postMessage(
                            {
                                forbiddenDataMessage: "您已被移除群聊",
                            },
                            "*"
                        );
                        return false;
                    }
                    this.$apis.memberJinyan({
                        GroupId: this.userInfo.roomId,
                        Members_Account: [e.data.forbiddenData.memberId],
                        ShutUpTime:
                            e.data.forbiddenData.type === 1 ? 0 : 999999,
                    });
                }
                if (e.data.importMessage) {
                    this.importData = e.data;
                    this.$store.commit("updateImportMessage", true);
                }
                if (e.data.clickJinyanBtn) {
                    this.$store.commit("updateClickJinyanBtn", true);
                }
            });
        },
        // 计算未读消息并发送消息到父级页面
        /**
         * @param { type } 当前窗口状态
         * @param { isShowLayer } 是否展示父级页面的消息提示
         * @param { msg } 消息内容
         */
        async sendMsgToParent(type, msg) {
            let str = "";
            if (type !== "WINDOW_MIN") {
                await this.udpateUnReadMessage();
            } else {
                str = `<div style="font-weight: blod">您收到一条新消息</div>`;
            }
            console.log(
                msg,
                str,
                type,
                "计算未读消息并发送消息到父级页面==========================================》"
            );
            let natList = this.getChild("conversation").conversations;
            console.log(
                "获取到了数据---------------------------------->",
                natList
            );
            let count = 0;
            for (const item of natList) {
                count += Number(item.unreadCount);
            }
            console.log("数据计算完成----------->", count);
            window.parent.postMessage(
                {
                    type,
                    count,
                    str,
                    isShowLayer: type === "WINDOW_MIN" ? true : false,
                },
                "*"
            );
        },
    },
};
</script>
<style lang="less" scoped>
@import "./web-chat.less";
</style>
